$color_semi-black: rgba(21, 21, 31, 1);
$color_gray: #7a7a7a;
$color_white: #ffffff;
$color_black: black;
$font-family_1: "Inter";
$background-color_white: #fff;
$background-color_yellow: #f5e341;
$background-color_gray: #f1f1f1;

@mixin phone { 
	@media (min-width: 320px) {
		@content; 
	} 
}

@mixin tablet { 
	@media (min-width: 768px) {
		@content; 
	} 
}

@mixin laptop { 
	@media (min-width: 1024px) {
		@content; 
	} 
}

@mixin  desktop { 
	@media (min-width: 1440px) {
		@content; 
	} 
}



body {
	width: 100%;
	height: 100%;
}

.btn {
	cursor: pointer;
}
.header { 
	background-color: $background-color_white; 
	height: 64px; 

	&__wrapper { 
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 100%; 

		@include laptop {
			justify-content: normal;
		}
	}

	&__nav {
		@include laptop {
			display: flex;
		}
	}
	
	&__lang {
		margin-left: auto;
	}

	&__sign-up {
		&:hover {
			background: rgba(250, 226, 26, 1);
		}
		&:active {
			background: rgba(234, 203, 19, 1);
		}
		margin-left: 8px;
		padding: 16px 24px;
		border-radius: 38px;
		background-color: $background-color_yellow;
		font-family: $font-family_1;
		font-weight: 600;
		font-size: 1em;

		@include phone {
			margin-left: auto;
		}

		@include tablet {
			margin-left: 8px;
		}
	}

	&__login {
		padding: 14px 21.5px;
		border-radius: 38px;
		border: 1px solid #c1c1c1;
		color: $color_black;
		background-color: $background-color_white;
		font-family: $font-family_1;
		font-size: 1em;
		font-weight: 600;
		display: flex;
		align-items: center;
		margin-left: 30px;

		&:hover {
			border: 1px solid #0c0c0c;
		}
		&:active {
			border: 0;
			background: rgba(237, 237, 238, 1);
			border: 1px solid rgba(237, 237, 238, 1);
		}
		@include phone {
			display: none;
		}

		@include tablet {
			display: flex;
			margin-left: auto;
		}

		@include laptop {
			margin-left: 30px;
		}
	}

	&__logo {
		font-size: 3em;
	}

	&__burger {
		background-image: url("../img/bg-menu.svg");
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		width: 24px;
		height: 16px;

		@include phone {
			margin-left: 15px;
		}

		@include laptop {
			display: none;
		}
	}
}

.open {
	.nav {
		&__item {
			&:hover {
				background: rgb(255, 255, 255);
				border-radius: 27px;
				padding: 0 0;
				border-radius: 0;
			}
			&:active {
				background: rgb(255, 255, 255);
				border-top: 27px;
				padding: 0 0;
				border-radius: 0;
			}
		}
		&__link {
			&:hover {
				background: rgb(255, 255, 255);
				border-radius: 27px;
				color: $color_semi-black;
			}
		}

		&__menu {
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			width: 345px;
			height: 422px;
			background: #ffffff;
			z-index: 6;
			border-radius: 12px;

			@include tablet {
				width: 437px;
				height: 471px;
			}
		}
	}
	.language {
		&:hover {
			border-radius: 23px;
			background: rgba(237, 237, 238, 1);
			padding: 1px 6px;
			border: 1px solid #c1c1c1;
			border-radius: 12px;
			margin: 0;
		}
		padding: 1px 6px;
		&:active {
			border-radius: 23px;
			background: rgba(237, 237, 238, 1);
			padding: 1px 6px;
			border: 1px solid #c1c1c1;
			border-radius: 12px;
			margin: 0;
		}
	}

	.sidebar {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 70px;
		right: 15px;
	}

	.section {
		height: 100%;
		width: 100%;
		background: #7a7a7a;
		z-index: 2;
	}

	.header {
		&__burger {
			background-image: url(../img/close.svg);
			height: 24px;
			width: 24px;
		}

	}
}

.language {
	&:hover {
		border-radius: 23px;
		background: rgba(237, 237, 238, 1);
		padding: 10px 10px;
	}
	&:active {
		border-radius: 23px;
		background: rgba(237, 237, 238, 1);
		padding: 10px 10px;
	}

	padding: 10px 10px;
	background: rgba(255, 255, 255, 1);
	border: none;
	color: $color_semi-black;
	font-family: $font-family_1;
	font-size: 1em;
	font-weight: 600;
	margin-left: auto;

	@include laptop {
		display: flex;
		align-items: center;
		gap: 15px;
	}
}
.lang-w {
	font-family: $font-family_1;
	font-weight: 500;
	font-size: 1em;
}

.nav {
	margin-left: 40px;

	&__item {
		&:hover {
			background: rgb(255, 255, 255);
			border-radius: 27px;
			padding: 0px 0px;
		}
	}

	&__link {
		&:hover {
			background: rgba(245, 227, 65, 1);
			padding: 10px 12px;
			border-radius: 27px;
			color: $color_semi-black;
		}
		padding: 10px 12px;
		font-family: $font-family_1;
		font-weight: 600;
		font-size: 1em;
		color: $color_gray;
		text-decoration: none;
		&:active {
			background: rgba(234, 203, 19, 1);
			padding: 10px 12px;
			border-radius: 27px;
		}
	}

	&__list {
		display: flex;
		align-items: center;
	}

	@include phone {
		display: none;
	}
}

.dropdown {
	display: none;

	&-content {
		@include laptop {
			display: none;
			position: absolute;
			background-color: $background-color_white;
			width: 131px;
			z-index: 1;
			border: 1px solid rgba(224, 225, 226, 1);
			border-radius: 8px;
			a {
				color: $color_black;
				padding: 12px 16px;
				text-decoration: none;
				display: block;
				&:hover {
					background-color: $background-color_gray;
				}
			}
		}
	}

	@include laptop {
		margin-left: auto;
		position: relative;
		display: inline-block;
		&:hover {
			.dropdown-content {
				display: block;
			}
		}
	}
}

.sidebar {
	display: none;
	.nav {
		margin: 0;

		&__link {
			padding-left: 24px;
			font-family: $font-family_1;
			font-weight: 600;
			font-size: 1em;
			color: $color_gray;
			text-decoration: none;
		}

		&__list {
			display: flex;
			flex-direction: column;
			gap: 0;
		}

		&__item {
			border-bottom: 1px solid #e0e1e2;
			width: 345px;
			line-height: 52px;
			height: 52px;

			@include tablet {
				width: 437px;
				line-height: 64px;
				height: 64px;
			}
		}
	}
	
	.button {
		display: flex;
		flex-direction: column-reverse;
		gap: 8px;

		@include tablet {
			flex-direction: row-reverse;
			gap: 8px;
			align-items: center;
		}
	}

	.language {
		border: 1px solid #c1c1c1;
		border-radius: 12px;
		margin: 0;
		font-family: $font-family_1;
		font-weight: 600;
		font-size: 15px;
		display: flex;
		align-items: center;
		gap: 225px;

		@include tablet {
			font-size: 15px;
			gap: 310px;
		}
	}

	.header {
		&__sign-up {
			font-family: $font-family_1;
			font-weight: 600;
			font-size: 1em;
			padding: 16px 100px;
			background: #f5e341;
			border-radius: 38px;

			@include tablet {
				padding: 20px 63px;
				margin: 0;
			}
		}
		&__nav {
			display: flex;
		}

		&__login {
			display: block;
			padding: 16px 130px;
			font-family: $font-family_1;
			font-weight: 600;
			font-size: 1em;
			border: 1px solid #c1c1c1;
			border-radius: 38px;
			margin-bottom: 8px;

			@include tablet {
				padding: 20px 54px;
				margin: 0;
			}
		}
	}

	.arrow {
		margin-left: auto;
	}
}

.section {
	display: flex;
	justify-content: center;
}

.item {
	background: radial-gradient( 29.75% 70.68% at 73.76% 95.59%, rgba(255, 235, 51, 0.52) 0%, rgba(0, 0, 0, 0.2236) 100% ) , radial-gradient( 33.62% 79.88% at 51.09% 86.4%, rgba(255, 134, 66, 0.66) 0%, rgba(0, 0, 0, 0.2838) 100% ) , radial-gradient( 30.14% 71.61% at 68.54% 94.66%, rgba(106, 103, 218, 0.2) 0%, rgba(0, 0, 0, 0.086) 100% ) , radial-gradient( 29.17% 69.32% at 31.85% 96.96%, rgba(97, 71, 255, 0.73) 0%, rgba(0, 0, 0, 0.3139) 100% ) , radial-gradient( 40.36% 95.9% at 50% 70.37%, #ff8642 0%, rgba(0, 0, 0, 0.43) 100% ), #0e0e0e;

	&__title {
		color: $color_white;
		font-weight: 800;

		@include phone{
			font-size: 1.875em;
			line-height: 36px;
			margin-left: 24px;
			margin-top: 24px;
			z-index: 2;
		}

		@include tablet {
			font-size: 3em;
			margin-left: 64px;
			margin-top: 56px;
			line-height: 54px;
		}

		@include laptop {
			width: 545px;
			height: 108px;
		}

		@include desktop {
			margin-top: 136px;
			margin-left: 109px;
		}
	}

	&__text {
		color: #e0e1e2;
		font-weight: 500;

		@include phone {
			font-size: 0.9375em;
			line-height: 25px;
			letter-spacing: -0.015em;
			text-align: left;
			color: rgba(255, 255, 255, 0.6);
			margin: 8px 73px 32px 24px;
			width: 262px;
			height: 75px;
			z-index: 3;
		}

		@include tablet {
			font-size: 1.125em;
			margin-left: 64px;
			width: 429px;
			height: 84px;
		}

		@include desktop {
			margin-left: 109px;
		}
	}

	&__btn {
		@include phone {
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
		}

		@include tablet {
			justify-content: flex-start;
			gap: 15px;
			margin-left: 64px;
		}

		@include desktop {
			margin-left: 109px;
		}
	}

	&__cup {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
	}

	@include phone {
		width: 359px;
		height: 732px;
		border-radius: 24px;
		width: 359px;
		height: 732px;
		border-radius: 24px;
		margin-top: 8px;
	}

	@include tablet {
		width: 728px;
		height: 1016px;
	}

	@include laptop {
		width: 1008px;
		height: 1016px;
	}

	@include desktop {
		width: 1416px;
		height: 700px;
	}
}
.text {
	font-family: $font-family_1;
}

.container {
	margin: 0 auto;
	max-width: 1440px;

	@include phone {
		padding: 0 16px;
	}

	@include tablet {
		padding: 0 24px;
		margin: 0 auto;
	}

	@include laptop {
		padding: 0 25px;
		margin: 0 auto;
	}

	@include desktop {
		padding: 0 120px;
		margin: 0 auto;
		max-width: 1440px;
	}
}

